#login-container {
display:flex;
flex-direction: column;
justify-content: center;
align-items: center;
margin-top: 20px;
font-family: 'Open Sans', sans-serif;
font-weight: 300;
margin-left: 3%;
margin-right: 3%;
}

#login-form {
display:flex;
flex-direction: column;
justify-content: center;
align-items: center;
}

#login-btn {
  margin-top: 10px;
  color: white;
  background-color: #010D26;
  padding: 5px 10px;
  border-radius: 5px;  
}

#login-btn:hover {
  color: blanchedalmond;
} 

#login-link {
  text-decoration: none;
  color:#010D26;
  background-color: blanchedalmond;
  border-radius: 5px;
  padding: 5px 10px;  
}

